<template>
    <div class="page-index">
        <!-- <van-nav-bar            
            left-arrow
            title="提现记录"
            @click-left="onClickLeft"
        >    
        </van-nav-bar> -->
        <section v-if="!data || data.length == 0" class="none-list" >
            <p>抱歉，您目前没有提现记录</p>
            <img src="../../assets/images/pic_recording.png" />
        </section>
        <section  v-if="data || data.length > 0">
            <van-list
           
                v-model="loading"
                :finished="finished"
                finished-text=""
                @load="onLoad"
            >
                <ul class="list" v-if="data">
                    <li v-for="(item,key) in data" :key="key" >
                        <div class="flex justify-space-between">
                            <div class="left">
                                <p class="name" :class="{'red':item.status == 2}">{{item.subject}}</p>
                                <p>{{item.create_time}}</p>
                            </div>
                            <p class="right">{{item.money}}</p>
                        </div>
                        <p class="remarks">{{item.remarks}}</p>
                        <van-divider />
                    </li>
                
                </ul>
            </van-list>
        </section>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            data:'',
            loading:false,
            finished:false,
            pageInfo:{
                page_size:10,
                page_index:0
            }         
        }
    },
    created(){
        document.title = '提现记录'
        this.getPpplyCashLog();
    },
    methods:{
        onClickLeft(){
            this.$router.go(-1)
        },
        onLoad(){
            this.getPpplyCashLog();
        },
        getPpplyCashLog(){

            this.api.getPpplyCashLog({
                token:this.$store.state.userData,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
              
                this.loading = false;
                if(res.err_code == 0){
                    this.pageInfo.page_index++;
                    if(!res.data.length){
                        this.finished = true;
                    }
                    this.data = [...this.data,...res.data] 
                }
            })
        },
        handleTx(){
            if(this.intBalance >= 2){

            }
        },

    }
}
</script>
<style lang="less" scoped>
.page-index{
    .none-list{
        text-align: center;
        p{
            font-size: 15px;
            color: #888888;
            margin-top: 65px;
            margin-bottom: 41px;
        }
        img{
            width:238px;
            height:185px;
        }
    }
    .list{
        padding: 15px;

        li{ 

            padding: 23px 0 0px;
      
            .left{
                p{
                    color: #888888;
                    font-size: 12px;
                }
                .name{
                    color: #0DC6F2;
